import Vue from 'vue'
import Vuex from 'vuex'

// 
import base from './modules/base'
// 
import sign from './modules/sign-in-out'
// 
import home from './modules/home'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    base,
    sign,
    home
  },
  strict: false
})



